import React, { Component } from 'react'

class Quotes extends Component {
  constructor(props) {
    super(props)
    const idxStart = 0
    this.state = {
      index: idxStart,
      next: this.getNextIndex(idxStart),
      move: false
    }
  }

  getNextIndex(idx) {
    if (idx >= this.props.quotes.length - 1) {
      return 0
    }
    return idx + 1
  }

  setIndexes(idx) {
    this.setState({
      index: idx,
      next: this.getNextIndex(idx)
    })
  }

  componentDidMount() {
    setInterval(() => {
      this.setState({
        move: true
      })

      setTimeout(() => {
        this.setState({
          move: false
        })
        this.setIndexes(this.getNextIndex(this.state.index))
      }, 500)
    }, 5000)
  }

  render() {
    const move = this.state.move ? 'move' : '';
    return (
      <div className="quotes-container">
        <div className="quote-text absCenter">
          This is the text for the quote. And it is very long. And it goes on for a few lines.
            </div>
        <div className="quote-author absCenter">
          - Author 1
        </div>
        <style jsx>{`
          .quotes-container {
            position: relative;
            color: white;
            font-family: Righteous, Sans-Serif, Arial;
            letter-spacing: 1px;
            font-size: 1.5rem;
            height: 12rem;
          }
          .quote-text {
            border-left: 5px solid lightgrey;
            max-width: 26rem;
            padding: 1rem 1rem 0rem 1rem;
          }
          .quote-author {
            text-align: right;
            max-width: 26rem;
            font-family: Snippet, Sans-Serif, Arial;
          }
          .current {
            
          }
        `}</style>
      </div>
    )
  }
}

export default Quotes